.page-tree {
  list-style: none; }

.treeview .list-group-item {
  cursor: pointer; }

.treeview span.indent {
  margin-left: 10px;
  margin-right: 10px; }

.treeview span.icon {
  width: 12px;
  margin-right: 5px; }

.treeview .node-disabled {
  color: silver;
  cursor: not-allowed; }

.node- {
  color: #555;
  border: none; }

.node-:not(.node-disabled):hover {
  background-color: #f9f9f9; }

